.Hsl-empty {
    height: 15px;
}

.Hsl-hue-container {
    height: 30px;
    width: 270px;
}

.Hsl-hue-palet {
    height: 16px;
    width: 270px;
    background: -webkit-gradient(
        linear,
        left top, right top,
        color-stop(0, #e1451d),
        color-stop(17%, #fdff47),
        color-stop(50%, #86f9fe),
        color-stop(65%, #2900f8),
        color-stop(74%, #6e00f8),
        color-stop(83%, #e33df9),
        to(#e14423)
    );
    background: -webkit-linear-gradient(
        left,
        #e1451d 0,
        #fdff47 17%,
        #86f9fe 50%,
        #2900f8 65%,
        #6e00f8 74%,
        #e33df9 83%,
        #e14423 100%
    );
    background: -o-linear-gradient(
        left,
        #e1451d 0,
        #fdff47 17%,
        #86f9fe 50%,
        #2900f8 65%,
        #6e00f8 74%,
        #e33df9 83%,
        #e14423 100%
    );
    background: linear-gradient(
        to right,
        #e1451d 0,
        #fdff47 17%,
        #86f9fe 50%,
        #2900f8 65%,
        #6e00f8 74%,
        #e33df9 83%,
        #e14423 100%
    );
    border: 1px solid #d6d6d6;
    border-radius: 2px;
    -webkit-transform: translateY(2px);
        -ms-transform: translateY(2px);
            transform: translateY(2px);
    z-index: 2;
}

.Hsl-hue {
    position: absolute;
    border-radius: 2px;
    height: 20px;
    width: 12px;
    border: 1px solid #eaeaea;
    background-color: white;
    cursor: pointer;
    z-index: 3;
}

.Hsl-hue-area {
    height: 50px;
    width: 54px;
    position: absolute;
    top: -14px;
    left: -20px;
    -ms-touch-action: none;
        touch-action: none;
}

.Hsl-hue:active {
    cursor: move;
}

.Hsl-canvas {
    border: 1px solid #d6d6d6;
    border-radius: 2px;
}

.Hsl-main {
    -ms-touch-action: none;
        touch-action: none;
    position: absolute;
    background-color: rgba(255, 255, 255, 0.15);
    width: 20px;
    height: 20px;
    border: 3px #d6d6d6 solid;
    border-radius: 13px;
}


