body.light-theme  {
    background-color: #f1f1f1;
}

.light-theme .Client {
    background-color: white;
    -webkit-box-shadow: 0 0 10px #c5c5c5;
            box-shadow: 0 0 10px #c5c5c5;
    border-radius: 3px;
}

.light-theme .Client.empty {
    -webkit-box-shadow: none;
            box-shadow: none;
    background-color: #dcdcdc;
}

.light-theme .empty-Client-placeholder-title {
    color: #737373;
}

.light-theme .empty-Client-placeholder-ui a {
    color: #387ee5;
}

.light-theme .Client-title {
    color: #353535;
}

.light-theme .Client-edit {
    color: #b5b5b5;
}

.light-theme .Client-edit:hover {
    color: #868686;
}

.light-theme .icon-power.on .icon-power-inner,
.light-theme .icon-power.on .icon-power-outer {
    fill: green;
}

.light-theme .icon-power.off .icon-power-inner,
.light-theme .icon-power.off .icon-power-outer {
    fill: rgb(160, 160, 160);
}

.light-theme .Notification {
    background-color: #fbfbfb;
    border-color: #e2e2e2;
}

.light-theme .Menu {
    border-top-color: #e2e2e2;
    background-color: white;
}
.light-theme .Menu-pull-tab {
    border-color: #e2e2e2;
    background-color: white;
}

.light-theme .Menu-overlay {
    background-color: #eaeaea;
}

.light-theme .Menu-overlay div {
    color: #4d4d4d;
}

.light-theme .Switch {
    background-color: #eaeaea;
    border-color: #eaeaea;
}

.light-theme .Switch.on {
    background-color: #6eeb43;
    border-color: #6eeb43;
}

.light-theme .Switch-circle {
    background-color: white;
    border-color: #e8e8e8;
}

.light-theme .Switch.on .Switch-circle {
    border-color: #6eeb43;
}

.light-theme .Switch-circle-content {
    color: #808080;
}

.light-theme .MenuItem-title {
    color: #313131;
}

.light-theme .IPSettings-label {
    color: black;
}

.light-theme .IPSettings-input {
    background-color: white;
    border-color: #b3b3b3;
    color: black;
}

.light-theme .IPSettings-input:focus {
    border-color: #008000;
}

.light-theme .IPSettings-input.error {
    border-color: red;
}

.light-theme .PowerSwitch-svg {
    fill: #eaeaea;
}

.light-theme .PowerSwitch-svg.on {
    fill: #51cc47;
}

.light-theme .Slider-track {
    fill: #eaeaea;
}

.light-theme .Slider-button-visible {
    fill: white;
    stroke: #d4d4d4;
    stroke-width: 2;
}

.light-theme .Slider-indicator.step0 {
    fill: #eaeaea;
}
.light-theme .Slider-indicator.step1 {
    fill: #dbe7da;
}
.light-theme .Slider-indicator.step2 {
    fill: #cbe4c9;
}
.light-theme .Slider-indicator.step3 {
    fill: #bce1b9;
}
.light-theme .Slider-indicator.step4 {
    fill: #addea9;
}
.light-theme .Slider-indicator.step5 {
    fill: #9edb99;
}
.light-theme .Slider-indicator.step6 {
    fill: #8ed888;
}
.light-theme .Slider-indicator.step7 {
    fill: #7fd578;
}
.light-theme .Slider-indicator.step8 {
    fill: #70d268;
}
.light-theme .Slider-indicator.step9 {
    fill: #60cf57;
}
.light-theme .Slider-indicator.step10 {
    fill: #51cc47;
}


